.job-definition__container {
  align-items: center;
  flex-wrap: nowrap;
  column-gap: 10px;
  width: 100%;
}

.job-resposibilities {
  row-gap: 3px;
  width: 100%;
}
.job-resposibilities > li {
  list-style: inside;
}

.education__container {
  row-gap: 15px;
  width: 100%;
  padding: 20px 0;
}

.courses__container {
  width: 100%;
  row-gap: 10px;
}

.courses-list {
  width: 100%;
  row-gap: 3px;
}

.courses-list__item > :first-child {
  flex-basis: 24px;
}
.courses-list__item > :last-child {
  flex-basis: auto;
}
.courses-list__item {
  align-items: center;
  column-gap: 10px;
}

.courses__link {
  width: auto;
  height: auto;
  font-style: normal;
  font-weight: 400;
  font-size: var(--font-size-addText);
  line-height: calc(var(--font-size-addText) * 1.2);
  letter-spacing: normal;
  color: var(--color-text-light);
}
.courses__link:hover {
  color: var(--color-text-dark);
  background-color: var(--color-highlight-light);
  border-radius: inherit;
}
.courses__link:active {
  color: var(--color-text-dark);
  background-color: var(--color-highlight-light);
  border-radius: inherit;
}

.eductaion__icon {
  width: 24px;
  height: 24px;
}

.code-example__container {
  row-gap: 15px;
  width: 100%;
  padding: 20px 0;
}

.task-description__container {
  row-gap: 10px;
  width: 100%;
}

.description__container {
  row-gap: 3px;
  width: 100%;
}

.code_highlight {
  padding: 1px 4px;
  color: var(--color-text-dark);
  background-color: var(--color-background-light);
  border-radius: 4px;
}

.description-details {
  max-width: 60vw;
}
.description-details:hover {
  color: var(--color-text-dark);
  background-color: var(--color-highlight-light);
  border-radius: inherit;
}
.description-details:active {
  color: var(--color-text-dark);
  background-color: var(--color-highlight-light);
  border-radius: inherit;
}
@media (max-width: 768px) {
  .description-details {
    max-width: 80vw;
  }
}

.projects__container {
  row-gap: 15px;
  width: 100%;
  padding: 20px 0;
}

.project-definition__container {
  row-gap: 3px;
}

.projects-link {
  font-style: normal;
  font-weight: 400;
  font-size: var(--font-size-H4);
  line-height: calc(var(--font-size-H4) * 1.2);
  letter-spacing: normal;
}

.footer {
  background: url("../../shared/assets/images/background/noise_transparent@2x.png"), var(--color-background-left);
}

.footer__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: space-between;
  justify-content: space-between;
  align-items: center;
}
.footer__list > * {
  flex-basis: auto;
}
.footer__list {
  padding: 10px 0;
}

.footer__item {
  color: var(--color-text-light);
}

.footer__link {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: space-between;
  justify-content: center;
  align-items: center;
}
.footer__link:hover, .footer__link:active {
  color: var(--color-highlight-light);
  animation: pulsation 1s infinite normal ease 0s;
}
.footer__link:hover .footer__icon:nth-child(1), .footer__link:active .footer__icon:nth-child(1) {
  visibility: hidden;
  width: 0;
  height: 0;
}
.footer__link:hover .footer__icon:nth-child(2), .footer__link:active .footer__icon:nth-child(2) {
  visibility: visible;
  width: inherit;
  height: inherit;
  animation: pulsation 1s infinite normal ease 0s;
}

.footer__icon:nth-child(2) {
  visibility: hidden;
  width: 0;
  height: 0;
}

.rsschool-logo {
  width: 70px;
  text-align: right;
}

.github-text {
  padding-top: 3px;
}