}

.courses__container {
  // Position

  // block-model
  width: 100%;
  row-gap: 10px;
  // Typography

  // Decoration

  // Animation

  // Various
}

.courses-list {
  // Position

  // block-model
  width: 100%;
  row-gap: 3px;
  // Typography

  // Decoration

  // Animation

  // Various
}

.courses-list__item {
  // Position

  // block-model
  @include ui_common.flex-two-childs-basis(
    $first-basis: 24px,
    $last-basis: auto
  );
  & {
    align-items: center;
    column-gap: 10px;
  }
  // Typography

  // Decoration

  // Animation

  // Various
}

.courses__link {
  // Position

  // block-model
  width: auto;
  height: auto;
  // Typography
  @extend %font-Cabin;
  @include ui_common.text(
    $size: var(--font-size-addText),
    $height: calc(var(--font-size-addText) * 1.2)
  );

  // Decoration
  color: var(--color-text-light);
  // Animation
  @extend %transition-basic;

  // Various
  &:hover,
  &:active {
    @extend %transition-basic;
  }

  &:hover {
    @extend %transition-basic;
    color: var(--color-text-dark);
    background-color: var(--color-highlight-light);
    border-radius: inherit;
  }

  &:active {
    @extend %transition-basic;
    color: var(--color-text-dark);
    background-color: var(--color-highlight-light);
    border-radius: inherit;
  }
}

.eductaion__icon {
  // Position

  // block-model
  width: 24px;
